.main_confirmed_container {
  display: flex;
  flex-direction: column;
  position: fixed;
  margin-top: 50px;
  margin-left: 50%;
  z-index: 3;
  align-items: center;
  justify-content: center;
  background-color: white;
  height: 90vh;
  width: 30%;
  padding-left: 10px;
}
@media screen and (max-width: 1000px) {
  .main_confirmed_container {
    margin-left: 25%;
    font-size: 0.9rem;
  }
}
@media screen and (max-width: 750px) {
  .main_confirmed_container {
    margin-left: 0;
    width: 100%;
  }
}
