.main-nav {
  background-color: orange;
  height: 2.5rem;
  padding: 5px;
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 3;
}
nav ul {
  display: flex;
  align-items: center;
  gap: 10px;
  justify-content: space-between;
}
nav ul li {
  list-style: none;
}
nav ul li .link {
  text-decoration: none;
  color: black;
  font-weight: 600;
  font-size: 1.1rem;
  padding: 2px;
  border-radius: 2px;
  transition: 0.4s ease;
}
nav ul li .link:hover {
  color: white;
  background: rgb(255, 145, 0);
}
nav ul :first-child {
  margin-right: 10px;
}
#homeLink {
  color: white;
  font-size: 1.5rem;
}
#homeLink:hover {
  color: white;
  background: rgb(255, 145, 0);
}
#link3 {
  display: flex;
  cursor: pointer;
}
@media screen and (max-width: 500px) {
  nav ul .link {
    font-size: 0.8rem;
  }
  #homeLink {
    font-size: 1rem;
  }
}
