@import url("https://fonts.googleapis.com/css2?family=Courier+Prime:wght@400;700&family=Permanent+Marker&family=Rubik:ital,wght@0,300;0,400;1,500&display=swap");

@import url("https://fonts.googleapis.com/css2?family=Courier+Prime:wght@400;700&family=Luckiest+Guy&family=Permanent+Marker&family=Rubik:ital,wght@0,300;0,400;1,500&display=swap");
.product-container {
  width: 100%;
  height: 100%;
  background-color: rgb(255, 255, 255);
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 50px;
  padding: 10px;
}

.product-item {
  width: 430px;
  border-radius: 10px;
  padding: 15px;
  background-color: white;
  margin-top: 0;
  display: flex;
  box-shadow: 0px 5px 15px 3px rgba(0, 0, 0, 0.35);
  gap: 5px;
  padding: 10px;
  transition: 500ms;
}
.product-item:hover {
  box-shadow: 0px 5px 15px 3px rgba(0, 0, 0, 0.5);
}
.product-item .button {
  margin-left: auto;
  background: transparent;
  border: none;
  border-radius: 5px;
  padding: 0px 7px;
  transition: 500ms ease;
  height: 52px;
}
.product-item .button:hover {
  background: orange;
}
.logo h5 {
  padding: 2px 0px 2px 0px;
}

.headerClass {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10px;
  position: relative;
}
.headerClass h1 {
  /*font-family: "Permanent Marker", cursive; */
  font-family: "Luckiest Guy", cursive;
  color: orange;
}
@media screen and (max-width: 1466px) {
  .product-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 50px;
    padding: 10px;
  }
}
@media screen and (max-width: 915px) {
  .product-container {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 50px;
    padding: 10px;
  }
  .main-model {
    top: 30%;
    left: 5%;
  }
  .product-item {
    width: 100%;
  }
  #link1 {
    display: none;
  }
  #link2 {
    display: none;
  }
  #link3 {
    font-size: 0.9;
  }
}
.headerClass .underline {
  background: orange;
  height: 3px;
  width: 3rem;
  position: absolute;
  border-radius: 2px;
  top: 92%;
  transition: 500ms ease;
}
.headerClass .Pizza-header:hover + .underline {
  width: 6rem;
}
#pasta-section {
  height: auto;
}
