.loading {
  display: flex;
  align-items: center;
  height: 10px;
  justify-content: center;
}
.loading::after {
  content: "";
  height: 70px;
  width: 70px;
  border-radius: 50%;
  border: 10px solid transparent;
  animation: loading 1s linear infinite;
  border-top-color: orange;
  margin-top: 30%;
}
@keyframes loading {
  to {
    transform: rotate(1turn);
  }
}
