.backdrop {
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  width: 100%;
  background: rgba(0, 0, 0, 0.144);
}

.main-model {
  position: fixed;
  top: 35%;
  left: 35%;
  height: 200px;
  width: 350px;
  z-index: 3;
  background-color: rgb(207, 207, 207);
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 3px;
}
.center {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.input2 {
  margin-left: 30px;
  padding: 2px;
  border-top: none;
  border-left: none;
  border-right: none;
  border-radius: 2px dotted rgb(64, 90, 238);
}
.inputs {
  border-radius: 2px dotted grey;
  margin-left: 30px;
  margin-bottom: 10px;
  padding: 4px;
  border-top: none;
  border-left: none;
  border-right: none;
  outline: none;
}

.inputs:focus,
.input2:focus {
  border-bottom: orange 2px solid;
  border-top: none;
  border-left: none;
  border-right: none;
}

.submit-btn {
  margin-left: 5px;
  padding: 5px;
  background-color: white;
  border: 1px solid black;
}
.submit-btn:hover {
  scale: 1.2;
  background-color: orange;
  color: white;
}
.hide-model {
  cursor: pointer;
  position: absolute;
  left: 5%;
  top: 5%;
  background: white;
  border-radius: 20px;
  padding: 1px;
}
@media screen and (max-width: 583px) {
  .main-model {
    left: 10%;
  }
}
