.main-cart {
  position: fixed;
  background-color: white;
  top: 40%;
  left: 29%;
  height: fit-content;
  min-width: 585px;
  z-index: 3;
}
.top-cart {
  display: flex;
  align-items: center;
  justify-content: center;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  background-color: orange;
}
.down-cart {
  min-height: 200px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
}
.container {
  margin: 0;
  margin-right: auto;
  display: grid;
  grid-column: repeat(1, 2fr);
  margin-bottom: 5px;
}
.main-product {
  margin: 2px;
  padding: 2px;
}
.btn {
  padding: 2px;
  font-size: 1rem;
  margin: 5px;
}
@media screen and (max-width: 818px) {
  .container {
    display: flex;
    flex-direction: column;
  }
  .down-cart {
    display: flex;
    flex-direction: column;
  }
  .main-cart {
    min-width: 100%;
    left: 0%;
  }
}
