.categories {
  margin-top: 2.5rem;
  height: 3rem;
  z-index: 3;
  width: 100%;
  background-color: white;
}
html {
  scroll-behavior: smooth;
}
.categories ul {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}
.categories ul li {
  margin-top: 1rem;
  list-style: none;
}
.categories ul li a {
  text-decoration: none;
  color: white;
  border-radius: 5px;
  background-color: orange;
  padding: 5px;
  transition: 600ms ease;
}
.categories ul li a:hover {
  background-color: rgb(255, 166, 0, 0.95);
  scale: 1.3;
  font-size: 17px;
}
